* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.header {
  height: 100vh;
  background-position: center;
  background-size: cover;
}
.main-heading {
  font-size: 300%;
  font-weight: 300;
  max-width: 400px;
  text-align: left;
  margin-bottom: 20px;
  text-transform: uppercase;
  position: absolute;
  top: 100px;
  left: 80px;
}

.hero-img-wrapper {
  max-width: 555px;
}
.hero-img {
  max-width: 95%;
  padding-right: 0;
  margin: 100px 0 0 10px;
  position: absolute;
  right: 0;
  bottom: 0;
}

img {
  border: 0;
  max-width: 80%;
  vertical-align: middle;
  display: inline-block;
}
.hero-btn {
  position: absolute;
  top: 40%;
  left: 5%;
}

@media screen and (max-width: 550px) {
  .main-heading {
    font-size: 250%;
  }
}

.signIn {
  width: 100%;
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
}
